.logo {
  -webkit-animation: logo-spin infinite 10s linear;
          animation: logo-spin infinite 10s linear;
  height: 10vh;
  margin-right: 20px;
}
.logopaused {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}
.header {
  background-color: #222;
  height: 10vh;
  padding: 1vh;
  color: white;
  text-align: left;
}
.title {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 100%;
  font-size: 1.5em;
}
.main {
  position: absolute;
  top: 12vh;
  bottom: 5vh;
  width: 100%;
  background-color: whitesmoke;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}
.footer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  position: absolute;
  background-color: gray;
  height: 5vh;
  width: 100%;
  bottom: 0;
  color: white;
}
@-webkit-keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}



